import Sora, { ConnectionOptions, DataChannelConfiguration } from "sora-js-sdk";
import { RtcSettings } from "../components/UnityCanvas";

type Props = {
    channelID: string;
    accessToken: string;
    rtcSettings: RtcSettings | undefined;
};

type AudioCodecType = "OPUS" | "LYRA";

type VideoCodecType = "VP9" | "VP8" | "AV1" | "H264" | "H265";

// 
export const SoraProvider = ({ channelID, accessToken, rtcSettings }: Props) => {
    const signalingURL = "wss://sora.sora-cloud.shiguredo.app/signaling";
    // const signalingURL = "wss://0001.sora.sora-cloud.shiguredo.app/signaling";
    const debug = false;
    const sora = Sora.connection(signalingURL, debug);
    const metadata = {
        access_token: accessToken,
    };
    const dataChannelConfig: DataChannelConfiguration[] = [
        {
            label: "#systemCall",
            direction: "sendrecv",
        },
        {
            label: "#documentImage",
            direction: "sendrecv",
        },
        {
            label: "#faceInfo",
            direction: "sendrecv",
        },
        {
            label: "#voiceVolume",
            direction: "sendrecv",
        },
    ];
    let audioCodec: AudioCodecType;
    let videoCodec: VideoCodecType;
    if (rtcSettings == undefined) {
        console.error("RtcSetting is undefined");
        audioCodec = "OPUS";
        videoCodec = "VP9";
    } else {
        audioCodec = rtcSettings.audio_code_type.toUpperCase() as AudioCodecType;
        videoCodec = rtcSettings.video_code_type.toUpperCase() as VideoCodecType;
    }
    const options: ConnectionOptions = {
        multistream: true,
        video: true,
        audio: true,
        dataChannels: dataChannelConfig,
        dataChannelSignaling: true,
        audioCodecType: audioCodec,
        videoCodecType: videoCodec,
    };
    const sendrecv = sora.sendrecv(channelID, metadata, options);

    return sendrecv;
};
